import React, { Component } from 'react';
import IngredientButton from '../IngredientButton/IngredientButton';
import classes from './Pasta.css';


class Pasta extends Component {
  

  render () {

    let transformedIngredients = Object.keys(this.props.pasta).map(shapeKey => {
      return (
      <IngredientButton
        clicked={this.props.selectPasta}
        selected={this.props.pasta[shapeKey].selected}
        key={shapeKey}
        type={shapeKey}
      >
        {shapeKey}
      </IngredientButton>
      );
    });
  

    return (
      <div className={classes.Pasta}>
        {transformedIngredients}
      </div>
      
    )
  }

}


export default Pasta;