import React, { Component } from 'react';
import classes from './IngredientButton.css';
const FARFALLE_ICON = require('../../../assets/images/farfalle.png');
const PENNE_ICON = require('../../../assets/images/penne.png');
const SHELL_ICON = require('../../../assets/images/shell.png');
const ELBOW_ICON = require('../../../assets/images/elbow.png');
const PARMESAN_ICON = require('../../../assets/images/parmesan.png');
const CHEDDAR_ICON = require('../../../assets/images/cheddar.png');
const GOUDA_ICON = require('../../../assets/images/gouda.png')

class IngredientButton extends Component {

  state = {
    selected: false,
  }
  render () {
    let img = null;
    switch (this.props.type){
      case ('farfalle'):
        img = FARFALLE_ICON;
        break;
      case ('penne'):
        img = PENNE_ICON;
        break;
      case ('shell'):
        img = SHELL_ICON;
        break;
      case ('elbow'):
        img = ELBOW_ICON;
        break;
      case ('parmesan'):
        img = PARMESAN_ICON;
        break;
      case ('cheddar'):
        img=CHEDDAR_ICON;
        break;
      case ('gouda'):
        img = GOUDA_ICON;
        break;
      default:
        break;
      
    }
    return (
      <button className = {this.props.selected ? [classes.IngredientButton, classes.Active].join(' ') : classes.IngredientButton}
        onClick={() => this.props.clicked(this.props.type)}>
        <img src={img} alt={this.props.type}></img>
        {this.props.children}
      </button>
    )
  }
}

export default IngredientButton;