import React from 'react';
import IngredientButton from '../IngredientButton/IngredientButton';

const cheese = (props) => {
  let transformedIngredients = Object.keys(props.cheeses).map(cheeseType => {
    console.log('[Cheese]', props.cheeses[cheeseType]);
    return (
    <IngredientButton
      clicked={props.selectCheese}
      selected={props.cheeses[cheeseType].selected}
      key={cheeseType}
      type={cheeseType}
    >
      {cheeseType}
    </IngredientButton>
    );
  });


  return (
    <div>
      {transformedIngredients}
    </div>
    
  )
}

export default cheese;