import React, { Component } from 'react';
import Pasta from '../../components/Ingredients/Pasta/Pasta';
import Cheese from '../../components/Ingredients/Cheese/Cheese';
//import Slider from '@material-ui/core/Slider';
import classes from './MacBuilder.css';
import Slider from '../../components/UI/Slider/Slider';



class MacBuilder extends Component {

  state = {
    ingredients: {
    pasta: {
      farfalle: {
        selected: false,
        amount: 0,
      },
      penne: {
        selected: false,
        amount: 0,
      },
      shell: {
        selected: false,
        amount: 0,
      },
      elbow: {
        selected: false,
        amount: 0,
      }
    },
    cheeses: {
      parmesan: {
        selected: false,
        amount: 0,
        type: 'hard',
        flavor: ['strong', 'nutty', 'savory'],
        texture: 'gritty'
      },
      cheddar: {
        selected: false,
        amount: 0,
        type: 'semi-hard',
        flavor: ['sharp', 'creamy'],
      },
      gouda: {
        selected: false,
        amount: 0,
        type: 'semi-hard',
        flavor: ['smoky', 'creamy']
      }
    }
    }

  }

  selectPasta = (pastaType) => {
    const prevSelected = this.state.ingredients.pasta[pastaType].selected;
    const updatedIngredients = {
      ...this.state.ingredients
    }
    updatedIngredients.pasta[pastaType].selected = !prevSelected
    this.setState({
      ingredients: updatedIngredients,
    });
  };

  getSelectedPastas = () => {
    const pasta = {
      ...this.state.ingredients.pasta
    }
    return Object.keys(pasta).filter(shapeKey => pasta[shapeKey].selected);
  }

  getSelectedCheeses = () => {
    const cheeses = {
      ...this.state.ingredients.cheeses
    }
    return Object.keys(cheeses).filter(shapeKey => cheeses[shapeKey].selected);
  }

  selectCheese = (cheeseType) => {
    const prevSelected = this.state.ingredients.cheeses[cheeseType].selected;
    const updatedIngredients = {
      ...this.state.ingredients
    }
    updatedIngredients.cheeses[cheeseType].selected = !prevSelected
    this.setState({
      ingredients: updatedIngredients,
    });
  };

  handleSliderChange = (event, newValue) => {
    this.setState({
      cheeseNumber: newValue
    })
  };
  
  
  render () {

    function valueLabelFormat(value) {
      return marks.findIndex(mark => mark.value === value) + 1;
    }
    
    const marks = [
      {
        value: 1,
        label: '1 cheese',
      },
      {
        value: 2,
        label: '2 Cheese',
      },
      {
        value: 3,
        label: '3 Cheese',
      },
      {
        value: 4,
        label: '4 Cheese',
      },
      {
        value: 5,
        label: '5 Cheese',
      },
    ];
    return (
      <div className={classes.MacBuilder}>
        
        <Pasta
          selectPasta={this.selectPasta}
          pasta={this.state.ingredients.pasta}
        />
        Selected Pasta: {this.getSelectedPastas().join(', ')}
        <Cheese
          selectCheese={this.selectCheese}
          cheeses={this.state.ingredients.cheeses}
          />
        Selected Cheeses: {this.getSelectedCheeses().join(', ')}
        <div className={classes.Slider}>
          <Slider
          valueLabelFormat={valueLabelFormat}
          //getAriaValueText={valuetext}
          marks={marks}
          text='How Cheesy are we feeling?'
          onChange={this.handleSliderChange}
          />
        {this.state.cheeseNumber}
        
        </div>
      </div>
    )
  }
}


export default MacBuilder;